body {
  text-align: center;
  font-family: sans-serif;
}
.container {
  border: 1px solid salmon;
  width: 400px;
  padding: 15px;
  margin: 0 auto;
  border-radius: 8px;
}
.container p {
  background-color: #eee;
  border-radius: 5px;
}
img {
  border-radius: 50%;
}
ul > li:nth-child(n + 1) {
  background-color: aqua;
}
